import React from "react";
import xIcon from "./assets/x.svg";

const hideBtn = () => {
  document.getElementById("popup").className =
    "z-0 bg-black bg-opacity-80 fixed inset-0 hidden items-center w-screen h-screen";
  setTimeout(() => {
    document.getElementById("popup").className =
      "transition-opacity opacity-0 z-0 bg-black bg-opacity-80 fixed inset-0 hidden items-center w-screen h-screen";
  }, 100);
};

const openPopup = ({ unit, title, content, img }) => {
  document.getElementById("popup").className =
    "transition-opacity opacity-100 z-40 bg-black bg-opacity-80 fixed inset-0 flex items-center w-screen h-screen";
  document.getElementById("pop_unit").innerText = unit;
  document.getElementById("pop_title").innerText = title;
  document.getElementById("pop_content").innerText = content;
  document.getElementById("pop_img").src = img;

  if (unit === "pixel")
    document.getElementById("pop_unit").className =
      "capitalize text-sm font-medium text-red-400";
  else if (unit === "atelier")
    document.getElementById("pop_unit").className =
      "capitalize text-sm font-medium text-blue-400";
  else if (unit === "atelier & pixel"){
    document.getElementById("pop_unit").className =
      "capitalize text-sm font-medium text-yellow-400";
  }
};

export default function Popup() {
  return (
    <div
      id="popup"
      className="transition-opacity opacity-0 z-0 bg-black bg-opacity-80 fixed inset-0 hidden items-center w-screen h-screen"
    >
      <div className="bg-black text-white w-5/6 xl:w-4/6 2xl:w-7/12 rounded-2xl mx-auto">
        <div className="sticky h-full w-full bg-opacity-5 bg-white px-6 py-8 sm:px-16 sm:py-20 rounded-2xl">
          <p
            id="pop_unit"
            className="capitalize text-sm font-medium text-red-400"
          >
            Pixel
          </p>
          <p id="pop_title" className="text-xl font-medium mt-2">
            Title, 타이틀
          </p>
          <img
            id="pop_img"
            className="mt-6"
            alt="포트폴리오 더보기 이미지"
          />
          <p id="pop_content" className="text-white mt-4">
            content
          </p>
          <div
            onClick={hideBtn}
            className="cursor-pointer absolute sm:p-1 top-7 right-5 sm:top-16 sm:right-16 z-50 w-6 sm:w-8"
          >
            <img src={xIcon} alt="x-button" />
          </div>
        </div>
      </div>
    </div>
  );
}

export { openPopup };
