import React from "react";

export default function Slide({ title, content, unit, img }) {
  let unitVar = ["", "text-sm "];

  if (unit === "pixel") {
    unitVar[1] += "text-red-500";
    unitVar[0] = "Pixel";
  } else if (unit === "atelier") {
    unitVar[1] += "text-blue-500";
    unitVar[0] = "Atelier";
  } else if(unit === "atelier & pixel"){
    unitVar[1] += "text-yellow-500";
    unitVar[0] = "Atelier & Pixel";
  }

  return (
    <div className="w-2/3 xl:w-1/2 cursor-pointer">
      <img className="select-none rounded-xl" width="960" height="640" src={img} alt="포트폴리오 이미지" />
      <div className="mt-4">
        <p className={unitVar[1]}>{unitVar[0]}</p>
        <p className="mt-1 font-medium text-xl">{title}</p>
        <p className="text-base mt-1 font-light text-gray-500">{content}</p>
      </div>
    </div>
  );
}
