import firebase from "firebase/app";
import "firebase/firestore";
import "firebase/storage";

var firebaseConfig = {
  apiKey: process.env.REACT_APP_API_KEY,
  authDomain: "edcan-main-web.firebaseapp.com",
  projectId: "edcan-main-web",
  storageBucket: "edcan-main-web.appspot.com",
  messagingSenderId: "286923194547",
  appId: "1:286923194547:web:686d890d142a393635b0c6",
  measurementId: "G-YBP55156N9",
};

firebase.initializeApp(firebaseConfig);

const firestore = firebase.firestore();
const storage = firebase.storage();

const uploadData = (data, img) => {
  var uploadTask = storage
    .ref()
    .child("pr/" + data.title + ".png")
    .put(img);
  uploadTask.on(
    "state_changed",
    (snapshot) => {
      var progress = (snapshot.bytesTransferred / snapshot.totalBytes) * 100;
      console.log(progress + "% done");
      if (snapshot.state === firebase.storage.TaskState.PAUSED)
        console.log("Upload is paused");
    },
    (error) => {
      console.error(error);
    },
    () => {
      uploadTask.snapshot.ref.getDownloadURL().then(function (downloadURL) {
        console.log(downloadURL);
        firestore
          .collection("portfolio")
          .doc(data.title)
          .set({
            ...data,
            img: downloadURL
          })
          .then(() => {
            alert("업로드 성공!");
            window.location.reload();
          })
          .catch((e) => {
            console.error(e);
          });
      });
    }
  );
};


export { firebase, firestore, storage, uploadData };
