import React, { useState, useEffect } from "react";
import { Link, useHistory } from "react-router-dom";
import Foot from "../Foot";
import "./Main.css";
import Atelier from "../assets/Atelier.svg";
import Pixel from "../assets/PIXEL_onDark.svg";
import Card from "../Card";
import arrow_down from "../assets/arrow_down.png";
import Flicking from "@egjs/react-flicking";
import { AutoPlay, Fade } from "@egjs/flicking-plugins";
import Slide from "../Slide";
import Popup, { openPopup } from "../Popup";
import { firestore } from "../firebase";

export default function Main() {
  const [hide, setHide] = useState(false);
  const [hidden, setHidden] = useState("");
  const [data, setData] = useState(null);
  const history = useHistory();

  useEffect(() => {
    const scrollEvent = () => {
      if (
        document.documentElement.scrollTop + 2 * window.innerHeight >=
        document.body.scrollHeight
      ) {
        setHide(true);
      } else {
        setHide(false);
      }
    };
    window.addEventListener("scroll", scrollEvent);
    getData();
    return () => {
      window.removeEventListener("scroll", scrollEvent);
    };
  }, []);

  useEffect(() => {
    if (hide)
      setTimeout(() => {
        setHidden("hidden ");
      }, 300);
    else
      setTimeout(() => {
        setHidden("");
      }, 300);
  }, [hide]);

  const getData = () => {
    let query = firestore.collection("portfolio");

    query.get().then((querySnapshot) => {
      let q = [];
      querySnapshot.forEach((doc) => {
        q.push(doc.data());
      });
      setData(q);
    });
  };

  return (
    <>
      <div id="main">
        <Link
          to="/apply"
          className={
            hidden +
            (hide ? "opacity-0 " : "opacity-100 ") +
            "transition duration-200 fixed bottom-8 right-8 hover:shadow-md shadow-xl py-4 px-8 sm:px-16 bg-white rounded-full font-medium text-black z-30"
          }
        >
          지원하기
        </Link>

        <div
          id="page0"
          className="flex items-center w-screen h-screen bg-center"
        >
          <div className="flex items-center w-screen h-screen bg-black bg-opacity-60">
            <div className="text-white font-medium tracking-widest w-full text-3xl text-center">
              <p>We're Creators.</p>
              <p className="mt-2 font-light">EDCAN</p>
            </div>
            <div className="bottom-0 absolute mb-6 w-full px-auto">
              <div className="z-0 flex flex-col justify-center items-center">
                <p className="text-white text-center">더 알아보기</p>
                <div className="flex-grow-0 mt-3">
                  <img
                    className="h-3 animate-bounce"
                    src={arrow_down}
                    alt="스크롤 안내 화살표"
                  />
                </div>
              </div>
            </div>
          </div>
        </div>

        <div id="page1" className="w-screen min-h-screen bg-center bg-fixed">
          <div className="flex items-center bg-black bg-opacity-30 min-h-screen">
            <div className="text-white w-11/12 sm:w-9/12 2xl:w-7/12 my-auto mx-auto font-bold leading-normal">
              <p className="text-2xl sm:text-5xl leading-normal">
                모바일 컨텐츠 개발 및 디자인
              </p>
              <p className="text-xl sm:text-2xl font-medium mt-12 leading-normal sm:leading-normal">
                EDCAN은 모바일 컨텐츠 동아리로, 모바일 어플리케이션과 웹사이트
                등을 기획하고, 개발하며 디자인합니다.
                <br />
                <br />
                전문적인 커리큘럼과 개발 유닛과 디자인 유닛간의 협업을 통해 제작
                능력 뿐만 아니라, 실제 실무 능력 또한 향상시킬 수 있습니다.
                <br />
                <br />
                우리의 목표는 모바일 컨텐츠를 통해 사람들에게 더 나은
                라이프스타일을 경험할 수 있도록 만드는 것입니다.
              </p>
            </div>
          </div>
        </div>

        <div className="w-screen bg-blue-400">
          <div className="text-white text-3xl py-24 w-11/12 sm:w-9/12 2xl:w-7/12 my-auto mx-auto font-bold">
            <img
              className="mt-8 w-min mx-auto sm:mx-0"
              src={Atelier}
              alt="개발자 유닛 로고"
            />
            <p className="text-3xl leading-normal mt-16 text-center sm:text-left">
              Atelier Unit
            </p>
            <p className="text-xl sm:text-2xl font-medium mt-4 leading-relaxed sm:leading-relaxed text-center sm:text-left">
              Atelier는 소프트웨어과가 지원할 수 있는 개발 유닛이며, 뛰어난 문제
              해결력과 창의력을 지닌 개발자를 양성합니다.
              <br />
              Atelier에 속한 부원은 다음 교육과정을 이수합니다.
            </p>

            <div className="mt-16 sm:-mx-12 lg:mx-0 grid grid-flow-row grid-cols-1 grid-rows-6 sm:grid-cols-2 sm:grid-rows-3 xl:grid-cols-3 xl:grid-rows-2 gap-6">
              <Card
                color="blue"
                title="C언어 심화 과정"
                content={`C언어의 포인터, 자료 구조(Data Structure) 등의 심화 과정을 이수합니다.\n\n\n* 1학년 교육과정과 연계`}
              />
              <Card
                color="blue"
                title="Kotlin 기본 과정"
                content={`Android 앱 개발에 사용되는 Kotlin 언어를 살펴봅니다.\n또한, C언어와 다른 점을 비교하여 객체지향 프로그래밍(OOP)의 개념도 동시에 살펴봅니다.`}
              />
              <Card
                color="blue"
                title="Android 앱 개발 기초 과정"
                content={`Kotlin과 Android Studio를 이용하여 직접 어플리케이션을 만들어봅니다.\n이 과정에서는 안드로이드 프로그래밍 전반에 대한 기본적인 지식을 습득할 수 있습니다.`}
              />
              <Card
                color="blue"
                title="웹 Front-End / Back-End 과정"
                content={`HTML, CSS, Javascript, React를 이용한 Front-End, Javascript, node.js를 이용한 Back-End 수업을 진행합니다.\n\n* 1학년 2학기 교육과정과 연계`}
              />
              <Card
                color="blue"
                title="Android 앱 개발 중/고급 과정"
                content={`Retrofit과 LiveData, Jetpack 등을 이용한 중/고급 Android 앱 개발 과정을 이수합니다.`}
              />
              <Card
                color="blue"
                title="크로스 플랫폼 앱 개발 과정"
                content={`Android와 iOS의 어플리케이션을 하나의 코드로 구현할 수 있는 크로스플랫폼 앱을 구현해봅니다.`}
              />
            </div>
          </div>
        </div>

        <div className=" w-screen bg-red-400">
          <div className="text-white text-3xl py-24 w-11/12 sm:w-9/12 2xl:w-7/12 my-auto mx-auto font-bold">
            <img
              className="mt-8 w-min mx-auto sm:mx-0"
              src={Pixel}
              alt="디자이너 유닛 로고"
            />
            <p className="text-3xl leading-normal mt-16 text-center sm:text-left">
              Pixel Unit
            </p>
            <p className="text-xl sm:text-2xl font-medium mt-4 leading-relaxed text-center sm:text-left sm:leading-relaxed">
              Pixel은 콘텐츠디자인과가 지원할 수 있는 디자인 유닛이며, 뛰어난
              분석력과 창의력을 지닌 디자이너를 양성합니다.
              <br />
              Pixel에 속한 부원은 다음 교육과정을 이수합니다.
            </p>

            <div className="mt-16 sm:-mx-12 lg:mx-0 grid grid-flow-row grid-cols-1 grid-rows-6 sm:grid-cols-2 sm:grid-rows-3 xl:grid-cols-3 xl:grid-rows-2 gap-6">
              <Card
                color="red"
                title="모바일 UI/UX 디자인 과정"
                content={`모바일 UI/UX 디자인에 대한 이론을 Adobe XD와 Sketch를 이용하여 실습해보며 학습합니다.`}
              />
              <Card
                color="red"
                title="웹 UI/UX 디자인 과정"
                content={`웹 디자인에 대한 기본적인 개념을 이해하고 디자인 프로그램을 이용하여 웹사이트를 제작합니다.`}
              />
              <Card
                color="red"
                title="서비스 클론 디자인 과정"
                content={`멜론, 토스, 유튜브와 같은 실제 서비스를 클론 디자인해보며 UI/UX에 대한 기본적인 개념을 수립합니다.`}
              />
              <Card
                color="red"
                title="협업 도구 및 Figma 사용 교육 과정"
                content={`Figma를 포함한 여러 디자인 협업 툴 사용 방법을 배우고, Atelier(개발자 유닛)와의 실제 협업을 준비합니다.\n\n`}
              />
              <Card
                color="red"
                title="프로토타입 제작 과정"
                content={`Adobe XD를 이용하여 실제로 작동하는 프로토타입을 제작해봅니다.`}
              />
              <Card
                color="red"
                title="UI/UX 디자인 심화 과정"
                content={`Adobe Illustrator, Adobe After Effects등의 사용법을 배우고, 이용해 인터렉션과 같은 고급 UI/UX 기술들을 구현해봅니다.`}
              />
            </div>
          </div>
        </div>

        <div className="py-32 select-none">
          <p className="text-3xl w-screen font-medium text-center mb-20">
            Portfolio
          </p>

          {data == null || (
            <Flicking
              tag="div"
              viewportTag="div"
              cameraTag="div"
              classPrefix="slide"
              deceleration={0.0075}
              onMoveEnd={(e2) => {
                if (e2.index === e2.currentTarget.getPanelCount() - 1) {
                  setTimeout(() => {
                    if (history.location.pathname !== "/PR")
                      e2.currentTarget.moveTo(0, 800);
                  }, 2000);
                }
              }}
              onSelect={(e) => {
                openPopup(data[e.index]);
              }}
              horizontal={true}
              circular={true}
              infinite={true}
              infiniteThreshold={0}
              lastIndex={Infinity}
              threshold={40}
              duration={800}
              panelEffect={(x) => 1 - Math.pow(1 - x, 3)}
              defaultIndex={0}
              inputType={["touch", "mouse"]}
              thresholdAngle={45}
              bounce={10}
              autoResize={true}
              adaptive={true}
              zIndex={1}
              overflow={false}
              hanger={"50%"}
              anchor={"50%"}
              gap={20}
              renderOnlyVisible={true}
              resizeOnContentsReady={true}
              moveType={{ type: "snap", count: 1 }}
              plugins={[new AutoPlay({ direction: "NEXT" }), new Fade()]}
            >
              {data.map((d) => (
                <Slide
                  unit={d.unit}
                  title={d.title}
                  content={d.content}
                  img={d.img}
                  key={d.title}
                />
              ))}
            </Flicking>
          )}

          <div className="flex mt-20 z-10">
            <Link
              to="PR"
              className="text-center focus:outline-none mx-auto px-20 py-4 ring-2 ring-black rounded-full font-medium"
            >
              전체보기
            </Link>
          </div>
        </div>
        <div className="w-screen mt-16 py-16 bg-white">
          <div className="w-full">
            <p className="text-center font-bold text-3xl sm:text-4xl">
              EDCAN이 더 궁금한가요?
            </p>
            <div
              id="last_1"
              className="mx-auto mt-16 px-5 sm:px-16 py-12 sm:py-16 w-11/12 sm:w-10/12 lg:w-9/12 xl:w-8/12 bg-black bg-opacity-80 rounded-md"
            >
              <div className="">
                <p className="w-fit text-2xl font-bold text-white">
                  지금 바로 지원하세요
                </p>
                <p className="w-fit mt-4 text-base font-normal text-white leading-relaxed">
                  최고의 동아리에서, 열정이 가득한 여러분을 기다립니다.
                  <br />
                  <b>소프트웨어과 1, 2학년</b>과, <b>콘텐츠디자인과 1, 2학년</b>
                  이 신청할 수 있습니다.
                </p>
                <div className="w-fit mt-5">
                  <Link
                    className="text-base font-medium text-blue-400"
                    to="/apply"
                  >
                    지원하기 {">"}
                  </Link>
                </div>
              </div>
            </div>
          </div>
        </div>

        <div className="pt-12 pb-20">
          <p className="text-center font-bold text-3xl sm:text-4xl">
            자주 묻는 질문
          </p>
          <p className="mt-12 text-center rounded-xl bg-gray-200 font-medium text-gray-600 px-6 py-3 mx-auto w-11/12 sm:w-10/12 lg:w-9/12 xl:w-8/12 leading-normal">
            찾는 질문이 없는 경우,{" "}
            <a
              href="https://www.facebook.com/edcancircle"
              target="_blank"
              rel="noopener noreferrer"
              className="inline text-blue-500 hover:text-blue-400"
            >
              에드캔 페이스북 페이지
            </a>
            로 문의해주시기 바랍니다.
          </p>
          <div className="text-center mx-auto w-11/12 sm:w-10/12 lg:w-9/12 xl:w-8/12">
            <div id="q1" className="mt-10 ">
              <p className="font-medium text-xl">
                Q. 포트폴리오는 필수 제출인가요?
              </p>
              <p className="mt-1">
                아닙니다. 포트폴리오는 면접에서 본인의 역량을 100% 볼 수
                없으므로 제출하는 것입니다.
                <br />꼭 제출할 필요는 없지만, 제출한다면{" "}
                <b>지원자님의 역량을 더 정확하게 파악할 수 있습니다.</b>
                <br />
                여기서 말하는 역량은, <b>
                  실력보다는 열정에 가깝다는 거!😉
                </b>{" "}
                (엄청난 걸 바라는게 아니에요!)
              </p>
            </div>
            <div id="q2" className="mt-8">
              <p className="font-medium text-xl">
                Q. 모바일 컨텐츠 동아리? 정확히 뭐하는 곳인가요?
              </p>
              <p className="mt-1">
                EDCAN은{" "}
                <b>
                  앱과, 웹을 주력으로 기획/제작(개발/디자인)하는 전문(전공)
                  동아리
                </b>
                입니다.
                <br />
                그에 맞춘 교육과, 개발자와 디자이너가 협업하는 다양한 프로젝트를
                진행합니다.
              </p>
            </div>
            <div id="q3" className="mt-8">
              <p className="font-medium text-xl">Q. 재밌나요?</p>
              <p className="mt-1">
                무언가 기획하고, 만드는 것을 좋아하신다면,{" "}
                <b>꿀잼각 보장드립니다😆</b>
              </p>
            </div>
          </div>
        </div>
      </div>

      <Popup />
      <Foot />
    </>
  );
}
