import React from "react";

export default function Card({ title, content, color }) {
  let cName = "bg-clip-text text-transparent bg-gradient-to-r";

  if (color === "blue") {
    cName += " from-indigo-500 to-blue-500";
  } else if (color === "red") {
    cName += " from-red-500 to-pink-500";
  } else {
    cName = "text-black";
  }

  return (
    <div className="bg-white text-black font-bold text-xl rounded-2xl px-8 py-8">
      <p className={cName}>{title}</p>
      <div className="text-base font-normal mt-2 whitespace-pre-line">
        {content}
      </div>
    </div>
  );
}
