import React from 'react';
import {Link} from 'react-router-dom';

export default function Error() {

    return (
      <>
        <div className="flex items-center bg-black opacity-90 w-full h-screen">
          <div className="text-center w-full font-medium text-2xl">
            <p className="text-white">존재하지 않는 페이지입니다.</p>
            <div className="mt-4">
              <Link
                to="/"
                className="text-gray-300 hover:text-gray-100 font-normal text-lg"
              >
                메인 화면으로 가기 {">"}
              </Link>
            </div>
          </div>
        </div>
      </>
    );

}