import React, { useState, useEffect } from "react";
import { useHistory } from "react-router-dom";
import { firestore } from "../firebase";
import Foot from "../Foot";
import Popup, { openPopup } from "../Popup";

export default function PR() {
  const history = useHistory();
  const [portfolio, setPortfolio] = useState("All");
  const [lineState, setLineState] = useState("w-1.6 ml-2");
  const [data, setData] = useState(null);

  useEffect(() => {
    window.scrollTo(0, 0);
    getData();
  }, []);

  const getData = () => {
    let query = firestore.collection("portfolio");

    query.get().then((querySnapshot) => {
      let q = [];
      querySnapshot.forEach((doc) => {
        q.push(doc.data());
      });
      setData(q);
    });
  };

  const changePortfolio = (stateNumber) => {
    switch (stateNumber) {
      case 1:
        setPortfolio("Atelier");
        setLineState("w-1/3 ml-13");
        break;
      case 2:
        setPortfolio("Pixel");
        setLineState("w-3/12 ml-34");
        break;
      case 0:
      default:
        setPortfolio("All");
        setLineState("w-1.6 ml-2");
    }
  };

  return (
    <div>
      <svg
        className="cursor-pointer w-6 absolute top-6 left-6"
        onClick={() => {
          history.goBack();
        }}
        xmlns="http://www.w3.org/2000/svg"
        fill="none"
        viewBox="0 0 24 24"
        stroke="#000000"
      >
        <path
          strokeLinecap="round"
          strokeLinejoin="round"
          strokeWidth={2}
          d="M15 19l-7-7 7-7"
        />
      </svg>
      <div>
        <p className="text-center text-2xl font-medium mt-20">Portfolio</p>
      </div>
      <div className="mx-auto w-min mt-12 mb-10 font-normal text-center">
        <div className="w-max">
          <div
            onClick={() => {
              changePortfolio(0);
            }}
            className={
              "tracking-wider inline-block px-3 cursor-pointer " +
              (portfolio === "All"
                ? "text-black"
                : "text-gray-400 hover:text-gray-600")
            }
          >
            All
          </div>
          <div
            onClick={() => {
              changePortfolio(1);
            }}
            className={
              "tracking-wider inline-block px-3 cursor-pointer " +
              (portfolio === "Atelier"
                ? "text-black"
                : "text-gray-400 hover:text-gray-600")
            }
          >
            Atelier
          </div>
          <div
            onClick={() => {
              changePortfolio(2);
            }}
            className={
              "tracking-wider inline-block px-3 cursor-pointer " +
              (portfolio === "Pixel"
                ? "text-black"
                : "text-gray-400 hover:text-gray-600")
            }
          >
            Pixel
          </div>
        </div>
        <hr
          className={
            "transition-all duration-500 mt-1 border-t-2 border-gray-700 " +
            lineState
          }
        />
      </div>

      <div className="mx-8 mb-12 grid grid-cols-1 sm:grid-cols-2 lg:mx-32 xl:mx-48 2xl:mx-72 gap-4">
        {data == null ||
          data.map((d) => {
            if (
              (portfolio === "Atelier" && d.unit === "pixel") ||
              (portfolio === "Pixel" && d.unit === "atelier")
            ) {
              return <div className="hidden" key={d.title}></div>;
            }
            return (
              <div className="relative" key={d.title}>
                <img
                  className="rounded-xl z-0"
                  src={d.img}
                  alt="포트폴리오 이미지"
                />
                <div
                  className="cursor-pointer transition-opacity duration-200 hover:opacity-100 opacity-0 w-full h-full bg-black bg-opacity-40 rounded-xl absolute inset-0 z-10"
                  onClick={() => {
                    openPopup(d);
                  }}
                >
                  <p className="capitalize absolute text-xs bottom-10 left-6 text-gray-200">
                    {d.unit}
                  </p>
                  <p className="absolute font-medium bottom-4 left-6 text-white">
                    {d.title}
                  </p>
                </div>
              </div>
            );
          })}
      </div>

      <Popup />

      <Foot />
    </div>
  );
}
